module Client.Messages

open System
open ServerCode.Domain

/// The messages processed during login 
type LoginMsg =
  | GetTokenSuccess of string
  | SetUserName of string
  | SetPassword of string
  | AuthError of exn
  | ClickLogIn

/// The different messages processed when interacting with the wish list
type WishListMsg =
  | LoadForUser of string
  | FetchedWishList of WishList
  | RemoveBook of Book
  | AddBook
  | TitleChanged of string
  | AuthorsChanged of string
  | LinkChanged of string
  | FetchError of exn

/// Messages when interacting with the flat Rubik
type RubikMsg =
  | TurnNorthCW
  | TurnNorthCCW
  | TurnSouthCW
  | TurnSouthCCW
  | TurnEastCW
  | TurnEastCCW
  | TurnWestCW
  | TurnWestCCW
  | TurnTopCW
  | TurnTopCCW
  | TurnBottomCW
  | TurnBottomCCW
  | Tick of DateTime

/// The different messages processed by the application
type AppMsg = 
  | LoggedIn
  | LoggedOut
  | StorageFailure of exn
  | OpenLogIn
  | LoginMsg of LoginMsg
  | WishListMsg of WishListMsg
  | RubikMsg of RubikMsg
  | Logout

/// The user data sent with every message.
type UserData = 
  { UserName : string 
    Token : JWT }

/// The different pages of the application. If you add a new page, then add an entry here.
type Page = 
  | Home 
  | Login
  | WishList
  | Rubik

let toHash =
  function
  | Home -> "#home"
  | Login -> "#login"
  | WishList -> "#wishlist"
  | Rubik -> "#rubik"



